<template>
    <div class="list row">
        <!-- search bar-->
        <div class="col-md-8">
             <div class="input-group mb-3">
                <input type="text" class="form-control" placeholder="search by title..." v-model="title" />
                <div class="input-group-append">
                    <button class="btn btn-outline-secondary" type="button" @click="serachTitle">Search</button>
                </div>
            </div> 
        </div>
      <!-- tutorials lists-->
        <div class="col-md-6">
            <h4>Tutorials List</h4>
            <ul class="list-group">
                <li class="list-group-item"
                    :class="{ active: index == currentIndex}"
                    v-for="(tutorial, index) in tutorials"
                    :key="index"
                    @click="setActiveTutorial(tutorial, index)">

                    {{ tutorial.title }}
                </li>
            </ul>
            <!-- REMOVE ALL BUTTON -->
            <button class="m-3 btn btn-sm btn-danger" @click="removeAllTutorials"> Remove All </button>
        </div>

        <div class="col-md-6">
            <div v-if="currentTutorial">
                <h4>Tutorial</h4>
                <div>
                    <label><strong>Title:</strong></label>{{ currentTutorial.title }}
                </div>
                <div>
                    <label><strong>Description:</strong></label> {{currentTutorial.description}}
                </div>
                <div>
                    <label><strong>Status:</strong></label> {{currentTutorial.published ? "Published" : "Pending" }}
                </div>

                <router-link :to="'/tutorials/' + currentTutorial.id" class="badge badge-warning">Edit</router-link>
            </div>

            <div v-else>
                <br />
                <p>Please click on a Tutorial...</p>
            </div>
        </div>
    </div>
</template>




<script>
import TutorialDataService from "../services/TutorialDataService";


export default {
    name: "tutorials-list",
    data() {
        return {
            tutorials: [],
            currentTutorial: null,
            currentIndex: -1,
            title: ""
        };
    },
    method: {

        //get all tutorials
        retrieveTutorials() {
            TutorialDataService.getAll()
            .then(response => {
                    this.tutorials = response.data;
                    console.log(response.data);
                }
            ).catch( e => {
                console.log(e);
            });
        },

        refreshList(){
            this.retrieveTutorials();
            this.currentTutorial = null;
            this.currentIndex = -1;
        },

        setActiveTutorial(tutorial, index){
            this.currentTutorial = tutorial;
            this.currentIndex = tutorial ? index : -1;
        },
        //Delete All tutorials
        removeAllTutorials() {
            TutorialDataService.deleteAll()
            .then(response => {
                console.log(response.data);
                this.refreshList();
                }
            ).catch(e => {
                console.log(e);
            });
        },
        //find tutorial by title
        searchTitle() {
            TutorialDataService.findByTitle(this.title)
            .then(response => {
                this.tutorials = response.data;
                this.setActiveTutorial(null);
                console.log(response.data);
            }).catch(e => {
                console.log(e);
            });
        }
    },
    mounted(){
        this.retrieveTutorials();
    }
};
</script>


<style>
.list{
    text-align: left;
    max-width: 750px;
    margin: auto;
}
</style>

